import { UPDATE_HP_ENEMY, CHANGE_ENEMY } from "reduxStore/actionTypes";
import enemiesList from "utils/enemiesList";

export const initialState = enemiesList[0];

export default function(state = initialState, action) {
  switch (action.type) {
    case UPDATE_HP_ENEMY: {
      const { changeInHP } = action.payload;
      return {
        ...state,
        hp: state.hp + changeInHP
      };
    }
    case CHANGE_ENEMY: {
      const { newEnemy } = action.payload;
      return {
        ...state,
        ...newEnemy
      }
    }
    default:
      return state;
  }
}
